import * as React from 'react';
import { User } from 'CustomTypings/schema';
import Avatar from 'Components/Avatar';

interface Props {
  me: User;
  // tslint:disable-next-line:no-any
  onClick?: any;
}

// tslint:disable-next-line:typedef
const UserProfileBox = (props: Props) => {
  
  return (
    <div
      className="uk-width-1-1 uk-text-center uk-padding-small"
    >
      <Avatar 
        size={70} 
        // tslint:disable-next-line:max-line-length
        url={props.me.avatar ? props.me.avatar.name : ''} presence={false} />

        <p className="uk-text-bold" style={{fontSize: 19}}>{props.me.firstname} {props.me.lastname}</p>
        <p style={{fontSize: 13}}>Founder at Gueva Technologies. Geek, programmer, interest in AI and Machine Learning, Big Data, love basketball.</p>
        <div className="uk-flex">
          <div className="score-wrap">
            <button className="uk-button uk-button-default">
              <p>0</p>
              <p>Posts</p>
            </button>
          </div>
          <div className="uk-inline">
            <button className="uk-button uk-button-default">
              <p>0</p>
              <p>Connections</p>
            </button>
          </div>
        </div>
        <div className="d-flex flex-row justify-content-between">
          <div className="score-wrap">
            <a href="/@rexraphael">
              <p className="big">0</p>
              <p className="small-title">Posts</p>
            </a>
          </div>
          <div className="score-wrap">
            <a href="/@rexraphael/followers">
            <p className="big">0</p>
            <p className="small-title">Connections</p>
            </a>
          </div>
        </div>
    </div>
  );
};

export default UserProfileBox;


